import React from "react"
import smoothScroll from "simplesmoothscroll"
const Navbar = () => {
  return (
    <div className="section">
      <div className="container">
        <div className="navbar-wrapper">
          <div
            role="button"
            onClick={() => smoothScroll("#home")}
            className="name"
            tabIndex={0}
          >
            Portfolio.
          </div>
          <div className="links-wrapper">
            <button onClick={() => smoothScroll("#work")}>Trabalho</button>
            <button onClick={() => smoothScroll("#about")}>Sobre</button>
            <button onClick={() => smoothScroll("#contact")}>Contato</button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Navbar
